body {
  background-color: #bbbbbb;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  background-color: #bbbbbb;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    
}

.vertAllignCont{
  position: relative;
}

.horiAllignCont{
  padding-top: 240px;
  padding-bottom: 240px;
  display: flex;

}

#chooseimg{

object-fit:inherit;
width: 100%;
height: 300px;
}
#dashboardbtn{
  border-radius: 25px;
  height: 200px;
  width: 120px;
  text-align: center;
  padding: 0px;
  vertical-align: middle;
  line-height: 185px;
  color: white;
  background-color: #424242;


}
#pokemonSelectText{
  text-align: center;
  font-size: larger;
  font-weight: bold;
}

#chooseimg:hover{
  box-shadow: 0 12px 16px 0 rgba(255, 0, 0, 0.24), 0 17px 50px 0 rgba(255, 0, 0, 0.19);

}
#cardStyling{
  outline-style: solid;
}
#dashboardbtn:hover{
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);

}
#pokedexTitle{
  text-align: center;
  color:black;
}
#tabledex{
  justify-self: center;
  margin-left: auto;
  margin-right: auto;
}
th{
  text-align: center;
}
td{
  text-align: center;
}

#navtitle{
  align-items: center;
  align-self: center;
  white-space: nowrap;
  width: auto;
  width: -webkit-fill-available;
  margin-left: 5%;
  margin-right: 5%;
}

thead {color:#ec407a;}
tbody {color:black}
tfoot {color:red;}